* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:root {
  --font: "Rubik", sans-serif;
  --os: "Oswald", sans-serif;
  --pt: "PT Sans", sans-serif;
  --neucha: "Neucha", sans-serif;
  --merri: "Merriweather", sans-serif;
}

body {
  position: relative;
  font-size: 16px;
  font-weight: 300;
  background-color: #fff;
  color: #222;
  margin: 0;
  min-width: 320px;
  width: 100%;
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  font-family: var(--pt);
  -ms-flex-direction: column;
  flex-direction: column;
}

body.fix {
  overflow-y: hidden;
}
body.unfix {
  overflow-y: auto;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
ul,
ol {
  margin: 0;
  padding: 0;
}
p {
  margin: 0;
}
img {
  display: block;
}
h1,
h2,
h3 {
  margin: 0;
}
dl,
dd {
  margin: 0;
}
button {
  background: none;
  cursor: pointer;
}


.h2 {

    font-weight: 400;
    font-size: 60px;
    letter-spacing: 1px;
    text-align: center;
    font-family: var(--os);    
    padding-top: 80px;
    padding-bottom: 80px;
    color: #000;
}


h3 {
  font-size: 100px;
}

@media (max-width: 768px) {

h3 {
  font-size: 60px;
}
}


